:root {
  margin: 0rem;
}

.home-logo {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 4vw;
  margin: 3vw 4vw;
}

.hero-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("https://images.unsplash.com/photo-1579208575657-c595a05383b7");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-text {
  background-color: rgba(220, 223, 227, 0.9);

  border-radius: 2rem;
  text-align: center;
  color: black /*  var(--primary-color) */;
  width: 30rem;
  max-width: 85vw;
  transform:translateY(10vh);
  padding: 1rem 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-text a {
  margin: 1rem 2rem;
}

.hero-text h1 {
  color: black /*  var(--primary-color) */;
}

.hero-text h3,
b {
  color: black /*  var(--primary-color) */;
  width: 90%;
}